<script>
//import check from "../check";

import NotificationTemplate from "./Notifications/NotificationTemplate";
import { BaseAlert } from "@/components";
import base from "../set";

import axios from "axios";
export default {
  components: {
    BaseAlert
  },
  data() {
    return {
      section: "",
      sections: "",
      stage: "",
      stages: "",
      division: "",
      divisions: "",
      notiTitle: "",
      notiText: "",
      studentName: "",
      studentNames: "",
      studentId: "",
      teacherName: "",
      teacherNames: "",
      teacherId: "",
      teacherName: "",
      type: "",
      notificationText: "",
      notificationTitle: "",
      allNotification: [{ notificationTitle: "loading..." }],
      allNotificationTeachers: [{ notificationTitle: "loading..." }]
    };
  },
  methods: {
    changeId() {
      // alert(this.studentId);
    },
    classNoti() {
      //alert(this.studentId);
      let url = base.baseUrl + "addStageNotification";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("section", this.section);
      params.append("stage", this.stage);
      params.append("division", this.division);
      params.append("notificationTitle", this.notificationTitle);
      params.append("notificationText", this.notificationText);
      params.append("type", this.type);

      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1) location.reload();
          else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    addTeacherNotification() {
      //alert(this.studentId);
      let url = base.baseUrl + "addTeacherNotification";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("teacherName", this.teacherName);
      params.append("type", this.type);
      params.append("notificationText", this.notificationText);
      params.append("teacherId", this.teacherId);

      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1) location.reload();
          else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },

    handleOk() {
      //alert(this.studentId);
      let url = base.baseUrl + "addStudentNotification";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("notificationTitle", this.notificationTitle);
      params.append("notificationText", this.notificationText);
      params.append("studentId", this.studentId);
      params.append("type", this.type);

      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1)
            self.notifyVue("bottom", "right", "success", response.data.msg);
          else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        component: NotificationTemplate,
        title: text,
        type: type,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        duration: 2000,
        speed: 1000,
        timeout: 0
      });
    },
    getSections() {
      let url = base.baseUrl + "getSections";
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.sections = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    getTeacherNames() {
      let url = base.baseUrl + "getTeacherNames";
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.teacherNames = response.data;
        })
        .catch(function(error) {
          alert(error);
        });
    },
    getStudentNames() {
      let url =
        base.baseUrl +
        "getStudentsNames?section=" +
        this.section +
        "&stage=" +
        this.stage +
        "&division=" +
        this.division +
        "";
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.studentNames = response.data;
        })
        .catch(function(error) {
          alert(error);
        });
    },
    getStages() {
      this.stage = "";
      this.division = "";
      let url = base.baseUrl + "getStages?section=" + this.section;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.stages = response.data;
        })
        .catch(function(error) {
          alert(error);
        });
    },
    getDivisions() {
      this.division = "";
      let url =
        base.baseUrl +
        "getDivisions?section=" +
        this.section +
        "&stage=" +
        this.stage;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.divisions = response.data;
        })
        .catch(function(error) {
          alert(error);
        });
    }
  },
  mounted() {
    this.i18n = this.$i18n;

    this.i18n.locale = "ar";
    this.$rtl.enableRTL();
    let url = base.baseUrl + "getStageNotifications";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        console.log(response);
        self.allNotification = response.data;
        //alert("d");
      })
      .catch(function(error) {
        alert(error);
      });

    url = base.baseUrl + "getTeacherNotifications";
    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        console.log(response);
        self.allNotificationTeachers = response.data;
        //alert("d");
      })
      .catch(function(error) {
        alert("sponse");
      });
  },
  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  }
};
</script>
