<script>
import EditProfileForm from "./Profile/EditProfileForm";
import UserCard from "./Profile/UserCard";
import base from "../set";
import axios from "axios";

export default {
  components: {},
  data() {
    return {
      fields: [
        {
          key: "section",
          label: "القسم",

          sortable: true
        },
        {
          key: "stage",
          label: "المرحلة",

          sortable: false
        },
        {
          key: "division",
          label: "الشعبة",
          sortable: true
          // Variant applies to the whole column, including the header and footer
        },
        {
          key: "action",
          label: "الشعبة",
          sortable: true
          // Variant applies to the whole column, including the header and footer
        }
      ],
      model: {
        sections: [],
        stages: [],
        divisions: [],
        teacherName: "",
        did: "",
        phone: "",
        teacherSex: "",
        teacherAddress: "",
        bloodType: "1",
        guardianPhone: "",
        teacherEmail: "",
        healthStatus: "1",
        idNumber: "1",
        teacherAge: "",
        teacherSection: "",
        teacherStage: "",
        teacherDivision: "",
        teacherBirthday: "",
        teacherDivs: [],
        dateOfReg: ""
      }
    };
  },
  mounted() {
    //    this.$router.push({ name: "login" });

    this.i18n = this.$i18n;
    if (this.enableRTL) {
      this.i18n.locale = "ar";
      this.$rtl.enableRTL();
    }
    //  this.initBigChart(0);

    let url = base.baseUrl + "getSections";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        // alert(response);
        console.log(response);

        self.model.sections = response.data;
      })
      .catch(function(error) {
        alert("sponse");
      });
  },
  // beforeDestroy() {
  //   if (this.$rtl.isRTL) {
  //     this.i18n.locale = 'en';
  //     this.$rtl.disableRTL();
  //   }
  // },
  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  },
  methods: {
    click() {
      let url = base.baseUrl + "addTeacher";
      //alert(url);
      var params = new URLSearchParams();
      //alert(this.model.teacherName);
      params.append("teacherName", this.model.teacherName);
      params.append("phone", this.model.phone);
      params.append("teacherSex", this.model.teacherSex);
      params.append("teacherAddress", this.model.teacherAddress);
      params.append("bloodType", "jj");
      params.append("teacherEmail", this.model.teacherEmail);
      params.append("idNumber", "88");
      params.append("teacherAge", "2");
      params.append("teacherBirthday", this.model.teacherBirthday);
      params.append("dateOfRegistration", this.model.dateOfRegistration);
      params.append("section", this.model.teacherSection);
      params.append("specialization", this.model.specialization);

      //   params.append("teacherBirthday", "2019-02-10");
      //   params.append("dateOfRegistration", "2019-02-10");
      var myObj = JSON.stringify(this.model.teacherDivs);

      params.append("teacherDivs", myObj);
      params.append("salary", this.model.salary);
      console.log(myObj);
      console.log(this.model.teacherSection);
      self = this;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1)
            self.notifyVue("bottom", "right", "success", response.data.msg);
          else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    getStages() {
      this.model.teacherStage = "";
      let url = base.baseUrl + "getStages?section=" + this.model.teacherSection;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.model.stages = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    getDivisions() {
      this.model.teacherDivisions = "";
      let url =
        base.baseUrl +
        "getDivisions?section=" +
        this.model.teacherSection +
        "&stage=" +
        this.model.teacherStage;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.model.divisions = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    teacherDiv() {
      // alert("ss");
      let url =
        base.baseUrl +
        "getTeacherSp?section=" +
        this.model.teacherSection +
        "&stage=" +
        this.model.teacherStage +
        "&division=" +
        this.model.teacherDivision;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          let checkDiv = 0;
          // alert(response);
          console.log(response);
          self.model.teacherDivs.forEach((value, index) => {
            console.log(value.id);
            if (value.id == response.data.id) checkDiv = 1;
            //console.log(index);
          });
          if (!checkDiv)
            self.model.teacherDivs.push({
              section: self.model.teacherSection,
              stage: self.model.teacherStage,
              division: self.model.teacherDivision,
              id: response.data.id
            });
          console.log(self.model.teacherDivs);
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    deleteTeacherDiv(index) {
      this.model.teacherDivs.splice(index, 1);
      console.log(self.model.teacherDivs);
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        group: "foo",
        title: text,
        text: "This is <b> content </b>",
        duration: "100",
        speed: 1000,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: "left",
        verticalAlign: "bottom",
        type: type,
        ignoreDuplicates: 1,
        Timeout: 800
      });
    }
  }
};
headers: new Headers({
  "content-type": "application/json",
  Accept: "application/json",
  "Access-Control-Allow-Credentials": true,
  "Access-Control-Allow-Origin": true
});
</script>
