<script>
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  mounted() {
    let url = base.baseUrl + "checkToken";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        // console.log(response);
        // alert(response.data.msg);
        if (response.data.true == 0) {
          localStorage.removeItem("auth");

          location.reload();
        }
      })
      .catch(function(error) {
        //alert("sponse");
      });
  }
};
</script>
