<template>
  <div class="row">
    <div class="col-12">
      <card v-if="show" :title="title">
        <div class="table-responsive">
          <!-- Main table element -->
          <b-button
            size="sm"
            style="    background: #e42dc885;"
            @click="info(row.item, row.index, $event.target)"
            class="mr-1"
            v-b-modal.modal-prevent-closing
          >
            اضافة جديد
          </b-button>
          <b-table
            empty-text="لا توجد اقسام , شعب , مراحل"
            show-empty
            small
            stacked="md"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
          >
            <template v-slot:cell(name)="row">
              {{ row.value.first }} {{ row.value.last }}
            </template>

            <template v-slot:cell(actions)="row">
              <b-button size="sm" @click="row.toggleDetails">
                {{ row.detailsShowing ? "اخفاء" : "عرض" }} البيانات
              </b-button>

              <b-button
                size="sm"
                style="background:rgba(212, 35, 35, 0.52);"
                @click="info(row.item, row.index, $event.target)"
                class="mr-1"
              >
                حذف
              </b-button>
            </template>

            <template v-slot:row-details="row">
              <b-card>
                <ul>
                  <li v-for="(value, key) in row.item" :key="key">
                    {{ key }}: {{ value }}
                  </li>
                </ul>
              </b-card>
            </template>
          </b-table>

          <template>
            <div>
              <b-modal
                id="modal-prevent-closing"
                ref="modal"
                title="  اضافة قسم او مرحلة جديدة"
                @show="resetModal"
                @hidden="resetModal"
                @ok="handleOk"
              >
                <form ref="form" @submit.stop.prevent="handleSubmit">
                  <b-form-group
                    :state="sectionState"
                    label="القسم"
                    style="text-align:right;"
                    label-for="section-input"
                    invalid-feedback="هذا الحقل مطلوب"
                  >
                    <b-form-input
                      id="section-input"
                      v-model="section"
                      :state="sectionState"
                      required
                    ></b-form-input>
                  </b-form-group>

                  <b-form-group
                    style="text-align:right;"
                    :state="stageState"
                    label="المرحلة"
                    label-for="stage-input"
                    invalid-feedback="هذا الحقل مطلوب"
                  >
                    <b-form-input
                      id="stage-input"
                      v-model="stage"
                      :state="stageState"
                      required
                    ></b-form-input>
                  </b-form-group>

                  <b-form-group
                    style="text-align:right;"
                    :state="divisionState"
                    label="الشعبة"
                    label-for="division-input"
                    invalid-feedback="هذا الحقل مطلوب"
                  >
                    <b-form-input
                      id="division-input"
                      v-model="division"
                      :state="divisionState"
                      required
                    ></b-form-input>
                  </b-form-group>
                </form>
              </b-modal>
            </div>
          </template>
        </div>
      </card>
    </div>
  </div>
</template>

<script>
import { BaseTable } from "@/components";
import base from "../set";

import axios from "axios";

const tableColumns = ["id", "section", "stage", "division", "delete"];

export default {
  components: {
    BaseTable,
    base
  },
  data() {
    return {
      section: "",
      stage: "",
      division: "",
      sectionState: null,
      stageState: null,
      divisionState: null,

      submittedNames: [],
      title: "الاقسام والشعب",
      show: false,
      items: [],
      fields: [
        {
          key: "section",
          label: "القسم",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "stage",
          label: " المرحلة",
          sortable: true,
          class: "text-center"
        },
        {
          key: "division",
          label: "الشعبة",
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true
        },
        { key: "actions", label: "Actions" }
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      filterOn: [],
      infoModal: {
        id: "info-modal",
        title: "",
        content: ""
      }
    };
  },
  mounted() {
    this.i18n = this.$i18n;
    if (this.enableRTL) {
      this.i18n.locale = "ar";

      this.$rtl.enableRTL();
    }
    // this.initBigChart(0);

    let url = base.baseUrl + "getClasses";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        // alert(response);
        console.log(response);

        self.items = response.data;
        self.show = true;
      })
      .catch(function(error) {
        alert("sponse");
      });
  },
  // beforeDestroy() {
  //   if (this.$rtl.isRTL) {
  //     this.i18n.locale = 'en';
  //     this.$rtl.disableRTL();
  //   }
  // },
  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  },
  methods: {
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.sectionState = valid;
      this.stageState = valid;
      this.divisionState = valid;

      return valid;
    },
    resetModal() {
      this.section = "";
      this.stage = "";
      this.division = "";
      this.sectionState = null;
      this.stageState = null;
      this.divisionState = null;
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault();

      // Trigger submit handler
      this.handleSubmit();
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.checkFormValidity()) {
        return;
      }
      // Push the name to submitted names
      this.submittedNames.push(this.name);
      //alert("good");
      // Hide the modal manually

      let url = base.baseUrl + "addClass";
      let baseToken = base.baseToken;
      let headers = {
        Token: baseToken
      };
      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("section", this.section);
      params.append("stage", this.stage);
      params.append("division", this.division);
      self = this;
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          alert(response.data.msg);
        })
        .catch(function(error) {
          alert("sponse");
        });

      this.$nextTick(() => {
        this.$bvModal.hide("modal-prevent-closing");
      });
    }
  }
};
</script>
