<template>
  <div class="row">
    <div class="col-12">
      <card v-if="show" :title="title">
        <div class="table-responsive">
          <!-- Main table element -->
          <b-button
            size="sm"
            style="    background: #e42dc885;"
            class="mr-1"
            v-b-modal.modal-prevent-closing
          >
            اختيار مرحلة و شعبة
          </b-button>
          <div v-b-modal.addTable class="plusIcon">
            <b-icon-plus></b-icon-plus>
          </div>
          <div>
            <b-tabs content-class="mt-3" align="center" v-model="cTab">
              <b-tab
                v-for="day in days"
                :title="day.day"
                @click="(items = []), getDaySchedule(day.id)"
              >
                <b-table
                  empty-text="لا يوجد محاضرات لعرضهم "
                  show-empty
                  small
                  :items="items"
                  :fields="fields"
                >
                  <template v-slot:cell(actions)="row">
                    <b-button
                      v-b-modal.modal-no-backdrop
                      size="sm"
                      @click="
                        $bvModal.show('bv-modal-example');
                        deleteLessonName = row.item.lesson;
                        deleteLessonId = row.item.id;
                        DeletedModelshow = true;
                      "
                      class="mr-1"
                      style="    background: rgba(212, 35, 35, 0.52);"
                    >
                      حذف
                    </b-button>
                  </template>
                </b-table>
              </b-tab>
            </b-tabs>
          </div>
          <template>
            <div>
              <b-modal
                id="addTable"
                ref="modal"
                title="  فرز"
                @show=""
                okTitle="رفع"
                cancelTitle="الغاء"
                @hidden="resetModal"
                @ok="addTable"
              >
                <div class="col-md-10 pr-md-1">
                  <b-form-group
                    id="input-group-3"
                    label="اليوم:"
                    label-for="input-3"
                  >
                    <b-form-select
                      id="input-3"
                      v-model="theDay"
                      :options="theDays"
                      required
                    ></b-form-select>
                  </b-form-group>
                </div>

                <div class="col-md-6 pr-md-1">
                  <label v-if="theDay"> عنوان المحاضرة</label>

                  <b-form-input
                    v-if="theDay"
                    v-model="lesson"
                    list="my-list-id1"
                  ></b-form-input>
                </div>

                <div class="col-md-4 pr-md-1">
                  <label v-if="theDay"> مدة المحاضرة</label>

                  <b-form-input
                    v-if="theDay"
                    v-model="duration"
                    list="my-list-id2"
                    @change="cTab = 0"
                  ></b-form-input>
                </div>
                <div class="col-md-4 pr-md-1">
                  <label
                    v-if="duration && lesson"
                    class="addSub"
                    @click="addSubject()"
                  >
                    اضف المحاضرة</label
                  >
                </div>
              </b-modal>

              <b-modal
                id="modal-prevent-closing"
                ref="modal"
                title="  فرز"
                @show=""
                @hidden="resetModal"
                @ok="handleOk"
              >
                <div class="col-md-10 pr-md-1">
                  <label style="text-align:right;"> القسم</label>
                  <b-form-input
                    placeholder=""
                    v-model="section"
                    @change="getStages()"
                    list="my-list-id"
                  ></b-form-input>

                  <datalist id="my-list-id">
                    <option v-for="section in sections">{{
                      section.section
                    }}</option>
                  </datalist>
                </div>

                <div class="col-md-6 pr-md-1">
                  <label> المرحلة</label>

                  <b-form-input
                    v-model="stage"
                    list="my-list-id1"
                    @change="getDivisions()"
                  ></b-form-input>

                  <datalist id="my-list-id1">
                    <option v-for="stage in stages">{{ stage.stage }}</option>
                  </datalist>
                </div>

                <div class="col-md-4 pr-md-1">
                  <label> الشعبة</label>

                  <b-form-input
                    v-model="division"
                    list="my-list-id2"
                    @change="cTab = 0"
                  ></b-form-input>

                  <datalist id="my-list-id2">
                    <option v-for="division in divisions">{{
                      division.division
                    }}</option>
                  </datalist>
                </div>
              </b-modal>
              <b-modal
                v-if="DeletedModelshow"
                id="bv-modal-example"
                hide-footer
              >
                <template v-slot:modal-title> اشعار <code>حذف</code> </template>
                <div class="d-block text-center">
                  <h3>
                    تأكيد <code>حذف</code> المحاضرة :
                    <code> {{ deleteLessonName }} </code>
                  </h3>
                </div>
                <b-button
                  style="background: #521a24;"
                  class="mt-3"
                  block
                  @click="deleteLesson()"
                  >حذف</b-button
                >
                <b-button
                  class="mt-3"
                  block
                  @click="$bvModal.hide('bv-modal-example')"
                  >اغلاق</b-button
                >
              </b-modal>
            </div>
          </template>
        </div>
      </card>
    </div>
  </div>
</template>

<script>
import { BaseTable } from "@/components";
import base from "../set";

import axios from "axios";

export default {
  components: {
    BaseTable,
    base
  },
  data() {
    return {
      DeletedModelshow: true,
      deleteLessonName: "",
      deleteLessonId: "",
      section: "",
      sections: "",
      stage: "",
      stages: "",

      cTab: 0,
      division: "",
      divisions: "",
      sectionState: null,
      stageState: null,
      divisionState: null,
      lesson: "",
      duration: "",
      submittedNames: [],
      lessons: [],
      durations: [],
      title: " الجدول الاسبوعي",
      show: true,
      currentDay: 1,
      days: [],
      items: [],
      theDay: "",
      theDays: [
        { text: "حدد اليوم", value: null },
        { text: "الاحد", value: "الاحد" },
        { text: "الاثنين", value: "الاثنين" },
        { text: "الثلاثاء", value: "الثلاثاء" },
        { text: "الاربعاء", value: "الاربعاء" },
        { text: "الخميس", value: "الخميس" },
        { text: "السبت", value: "السبت" }
      ],
      fields: [
        {
          key: "lesson",
          label: "المحاضرة",
          sortDirection: "desc"
        },
        {
          key: "hour",
          label: "المدة",
          class: "text-center"
        },

        { key: "actions", label: "حذف" }
      ]
    };
  },
  mounted() {
    this.i18n = this.$i18n;
    if (this.enableRTL) {
      this.i18n.locale = "ar";
      this.$rtl.enableRTL();
    }

    let url = base.baseUrl + "getSections";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        // alert(response);
        console.log(response);

        self.sections = response.data;
      })
      .catch(function(error) {
        alert("sponse");
      });
  },

  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  },
  methods: {
    addSubject() {
      this.lessons.push(this.lesson);
      this.durations.push(this.duration);
      this.lesson = "";
      this.duration = "";
      console.log(this.lessons);
      console.log(this.durations);
      this.notifyVue(
        "bottom",
        "right",
        "success",
        " تم اضافة المحاضرة , اضف محاضرة اخرى او اضغط رفع لاضافة اليوم"
      );
    },
    addTable() {
      let url = base.baseUrl + "addDaySchedule";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("section", this.section);
      params.append("stage", this.stage);
      params.append("division", this.division);
      params.append("lessons", this.lessons);
      params.append("durations", this.durations);
      params.append("day", this.theDay);

      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          self.lessons = [];
          self.durations = [];
          self.notifyVue("bottom", "right", "success", "تم الرفع");
        })
        .catch(function(error) {
          self.notifyVue(
            "bottom",
            "right",
            "danger",
            "لم يتم الرفع هناك خطأ ما "
          );
        });
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();

      return valid;
    },
    resetModal() {
      if (this.days.length > 0) {
        this.getDaySchedule(this.days[this.cTab].id);
      }
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing

      bvModalEvt.preventDefault();

      // Trigger submit handler
      this.handleSubmit();
    },
    handleSubmit() {
      // Exit when the form isn't valid

      // Push the name to submitted names
      this.submittedNames.push(this.name);
      //alert("good");
      // Hide the modal manually

      let url =
        base.baseUrl +
        "getSchedule?section=" +
        this.section +
        "&stage=" +
        this.stage +
        "&division=" +
        this.division;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          console.log(response);
          self.days = response.data;
          if (self.days.length >= 0) {
            if (self.cTab < 0) {
              self.getDaySchedule(self.days[0].id);
            } else self.getDaySchedule(self.days[self.cTab].id);

            console.log(self.cTab);
          }
        })
        .catch(function(error) {
          alert(error);
        });

      this.$nextTick(() => {
        this.$bvModal.hide("modal-prevent-closing");
      });
    },
    getStages() {
      this.stage = "";
      this.division = "";
      let url = base.baseUrl + "getStages?section=" + this.section;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.stages = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    getDivisions() {
      this.division = "";
      let url =
        base.baseUrl +
        "getDivisions?section=" +
        this.section +
        "&stage=" +
        this.stage;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.divisions = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    deleteLesson() {
      let url = base.baseUrl + "deleteLesson?lessonId=" + this.deleteLessonId;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .delete(url, {
          "Content-Type": "application/x-www-form-urlencoded;charset=utf-8",
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);
          //  alert("تم الحذف");
          if (response.data.true == 1) {
            self.notifyVue(
              "top",
              "center",
              "success",
              "تم حذف المادة :" + self.deleteLessonName
            );
            self.getDaySchedule(self.currentDay);

            self.DeletedModelshow = false;
          } else self.notifyVue("top", "center", "danger", "هناك خطأ ما !!");
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    getDaySchedule(dayId) {
      this.currentDay = dayId;
      let url = base.baseUrl + "getDaySchedule?id=" + dayId;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = self;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          console.log(response);
          self.items = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        group: "foo",
        title: text,
        text: "This is <b> content </b>",
        duration: "100",
        speed: 1000,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: "center",
        verticalAlign: "top",
        type: type,
        ignoreDuplicates: 1,
        Timeout: 800
      });
    }
  }
};
</script>
