<template>
  <div class="row">
    <div class="col-md-6">
      <card>
        <div class="places-buttons">
          <div class="row">
            <div class="col-md-6 ml-auto mr-auto text-center">
              <h4 class="card-title">
                رفع اعلان
              </h4>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-8 ml-auto mr-auto">
              <div class="row">
                <div class="col-md-6" style="    margin: auto;">
                  <base-button
                    v-b-modal.modal-prevent-closing
                    type="primary"
                    block
                  >
                    رفع اعلان
                  </base-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </card>
    </div>
    <div class="col-md-6">
      <card>
        <h4 slot="header">الاعلانات</h4>
        <base-alert
          dismissible
          v-for="adv in allAdv"
          :type="adv.type"
          dismissible
          :title="adv.advText"
        >
          <span
            ><b> {{ adv.advText }} </b></span
          >
        </base-alert>
      </card>
    </div>
    <div>
      <b-modal
        id="modal-prevent-closing"
        ref="modal"
        title="  فرز"
        style="text-align: right;   height: 540px !important; "
        @ok="handleOk"
      >
        <div style="text-align: right;" class="col-md-10 pr-md-1">
          <label style="text-align:right;">نص الاعلان</label>
          <b-form-input
            placeholder="نص الاعلان"
            v-model="advText"
          ></b-form-input>
        </div>

        <div style="text-align: right;" class="col-md-10 pr-md-1">
          <label style="text-align:right;"> نوع الاعلان</label>

          <b-form-select v-model="type" class="mb-3">
            <b-form-select-option value="info"
              >اعلان عادي
            </b-form-select-option>
            <b-form-select-option value="warning">تنبيه </b-form-select-option>

            <b-form-select-option value="danger"
              >اعلان عاجل</b-form-select-option
            >
          </b-form-select>
        </div>
      </b-modal>
    </div>
  </div>
</template>
<script>
//import check from "../check";

import NotificationTemplate from "./Notifications/NotificationTemplate";
import { BaseAlert } from "@/components";
import base from "../set";

import axios from "axios";
export default {
  components: {
    BaseAlert
  },
  data() {
    return {
      advText: "",

      type: "",
      allAdv: [{ advText: "loading..." }]
    };
  },
  methods: {
    handleOk() {
      //alert(this.studentId);
      let url = base.baseUrl + "addAdvertisement";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("advText", this.advText);
      params.append("type", this.type);

      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1) {
            self.notifyVue("bottom", "right", "success", response.data.msg);
            location.reload();
          } else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        component: NotificationTemplate,
        title: text,
        type: type,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        duration: 2000,
        speed: 1000,
        timeout: 0
      });
    }
  },
  mounted() {
    this.i18n = this.$i18n;

    this.i18n.locale = "ar";
    this.$rtl.enableRTL();
    let url = base.baseUrl + "getAdvertisements";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        console.log(response);
        self.allAdv = response.data;
        //alert("d");
      })
      .catch(function(error) {
        alert(error);
      });
  },
  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  }
};
</script>
<style>
div#modal-prevent-closing___BV_modal_content_ {
  height: auto !important;
}
.modal-content {
