<script>
import EditProfileForm from "./Profile/EditProfileForm";
import UserCard from "./Profile/UserCard";
import base from "../set";
import axios from "axios";

export default {
  components: {},
  data() {
    return {
      value: 0,
      max: 100,
      prog: 0,
      model: {
        sections: [],
        stages: [],
        divisions: [],
        studentName: "",
        phone: "",
        studentSex: "",
        studentAddress: "",
        bloodType: "",
        guardianPhone: "",
        studentEmail: "",
        healthStatus: "",
        idNumber: "",
        studentAge: "",
        studentSection: "",
        studentStage: "",
        studentDivision: "",
        studentBirthday: "",
        dateOfReg: ""
      }
    };
  },
  mounted() {
    //alert(Object.keys(this.model).length);
    //alert(this.model[4]);
    // let obj = this.model;
    // let resultArray = Object.keys(obj).map(function(key) {
    //   return [Number(key), obj[key]];
    // });

    // console.log(resultArray);
    // console.log(resultArray[4][1]);
    // console.log(resultArray.length);
    // this.prog = 0;
    // for (var i = 0; i < resultArray.length; i++) {
    //   if (resultArray[i][1] != "") {
    //     this.prog += 1;
    //   }
    // }
    // this.value = this.prog * 5.5;
    // alert(this.value);
    this.i18n = this.$i18n;
    this.i18n.locale = "ar";
    this.$rtl.enableRTL();

    //  this.initBigChart(0);

    let url = base.baseUrl + "getSections";
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        // alert(response);
        console.log(response);

        self.model.sections = response.data;
      })
      .catch(function(error) {
        alert("sponse");
      });
  },
  // beforeDestroy() {
  //   if (this.$rtl.isRTL) {
  //     this.i18n.locale = 'en';
  //     this.$rtl.disableRTL();
  //   }
  // },
  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  },
  methods: {
    textChangeProg() {
      let obj = this.model;
      let resultArray = Object.keys(obj).map(function(key) {
        return [Number(key), obj[key]];
      });

      this.prog = 0;
      for (var i = 0; i < resultArray.length; i++) {
        if (resultArray[i][1] != "") {
          this.prog += 1;
        }
      }
      this.value = this.prog * 5.5;
      if (this.value > 98) this.value = 100;
      console.log(this.value);
    },
    format(date) {
      var month = date.toLocaleString("en-US", { month: "short" });
      return date.getDate() + " " + month + " " + date.getFullYear();
    },
    click() {
      let url = base.baseUrl + "addStudent";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("studentName", this.model.studentName);
      params.append("phone", this.model.phone);
      params.append("studentSex", this.model.studentSex);
      params.append("studentAddress", this.model.studentAddress);
      params.append("bloodType", this.model.bloodType);
      params.append("guardianPhone", this.model.guardianPhone);
      params.append("studentEmail", this.model.studentEmail);
      params.append("healthStatus", this.model.healthStatus);
      params.append("idNumber", this.model.idNumber);
      params.append("studentAge", this.model.studentAge);
      params.append("studentSection", this.model.studentSection);
      params.append("studentStage", this.model.studentStage);
      params.append("studentDivision", this.model.studentDivision);
      params.append("studentBirthday", "2019-02-10");
      params.append("dateOfRegistration", "2019-02-10");
      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1)
            self.notifyVue("bottom", "right", "success", response.data.msg);
          else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    getStages() {
      this.model.studentStage = "";
      let url = base.baseUrl + "getStages?section=" + this.model.studentSection;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.model.stages = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    getDivisions() {
      this.model.studentDivisions = "";
      let url =
        base.baseUrl +
        "getDivisions?section=" +
        this.model.studentSection +
        "&stage=" +
        this.model.studentStage;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.model.divisions = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        group: "foo",
        title: text,
        text: "This is <b> content </b>",
        duration: "100",
        speed: 1000,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: "left",
        verticalAlign: "bottom",
        type: type,
        ignoreDuplicates: 1,
        Timeout: 800
      });
    }
  }
};
headers: new Headers({
  "content-type": "application/json",
  Accept: "application/json",
  "Access-Control-Allow-Credentials": true,
  "Access-Control-Allow-Origin": true
});
