<template>
  <div class="row">
    <div class="col-12">
      <card v-if="show" :title="title">
        <div class="table-responsive">
          <!-- Main table element -->
          <b-button
            size="sm"
            style="    background: #e42dc885;"
            @click="info(row.item, row.index, $event.target)"
            class="mr-1"
            v-b-modal.modal-prevent-closing
          >
            فرز الطلاب
          </b-button>
          <b-table
            empty-text="لا يوجد طلاب لعرضهم حسب هذ الفرز"
            show-empty
            small
            stacked="md"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
          >
            <template v-slot:cell(name)="row">
              {{ row.value.first }} {{ row.value.last }}
            </template>

            <template v-slot:cell(actions)="row">
              <b-button size="sm" @click="row.toggleDetails">
                {{ row.detailsShowing ? "اخفاء" : "عرض" }} البيانات
              </b-button>
            </template>

            <template v-slot:cell(abs)="row">
              <b-form-checkbox-group
                id="checkbox-group-2"
                v-model="selected"
                name="flavour-2"
              >
                <b-form-checkbox :value="row.item.id"></b-form-checkbox>
              </b-form-checkbox-group>
            </template>

            <template v-slot:row-details="row">
              <b-card>
                <ul>
                  <li v-for="(value, key) in row.item" :key="key">
                    {{ value }}
                  </li>
                </ul>
              </b-card>
            </template>
          </b-table>

          <template>
            <div>
              <b-modal
                id="modal-prevent-closing"
                ref="modal"
                title="  فرز"
                @show="resetModal"
                @hidden="resetModal"
                @ok="handleOk"
              >
                <div class="col-md-10 pr-md-1">
                  <label style="text-align:right;"> القسم</label>
                  <b-form-input
                    placeholder=""
                    v-model="section"
                    @change="getStages()"
                    list="my-list-id"
                  ></b-form-input>

                  <datalist id="my-list-id">
                    <option v-for="section in sections">{{
                      section.section
                    }}</option>
                  </datalist>
                </div>

                <div class="col-md-6 pr-md-1">
                  <label> المرحلة</label>

                  <b-form-input
                    v-model="stage"
                    list="my-list-id1"
                    @change="getDivisions()"
                  ></b-form-input>

                  <datalist id="my-list-id1">
                    <option v-for="stage in stages">{{ stage.stage }}</option>
                  </datalist>
                </div>

                <div class="col-md-4 pr-md-1">
                  <label> الشعبة</label>

                  <b-form-input
                    v-model="division"
                    list="my-list-id2"
                  ></b-form-input>

                  <datalist id="my-list-id2">
                    <option v-for="division in divisions">{{
                      division.division
                    }}</option>
                  </datalist>
                </div>
              </b-modal>
            </div>
          </template>
          <br />
          <br />
          <b-button
            v-show="selected.length > 0"
            size="sm"
            style="background: rgb(58, 65, 87);"
            @click="getAbsences()"
            class="mr-1"
          >
            رفع الغيابات
          </b-button>
          <div class="col-md-3 px-md-1">
            <b-form-input
              v-show="selected.length > 0"
              id="input-1"
              v-model="subject"
              type="text"
              required
              placeholder="ادخل المادة"
            ></b-form-input>
          </div>
        </div>
      </card>
      <div></div>
    </div>
  </div>
</template>

<script>
import { BaseTable } from "@/components";
import base from "../set";

import axios from "axios";

const tableColumns = ["id", "section", "stage", "division", "delete", "abs"];

export default {
  components: {
    BaseTable,
    base
  },
  data() {
    return {
      subject: "",
      DeletedModelshow: true,
      deleteStudentName: "",
      selected: [],
      deleteStudentId: "",
      section: "",
      sections: "",
      stage: "",
      stages: "",
      division: "",
      divisions: "",
      sectionState: null,
      stageState: null,
      divisionState: null,

      submittedNames: [],
      title: " الغيابات",
      show: true,
      items: [],
      fields: [
        {
          key: "studentName",
          label: "الاسم",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "section",
          label: "القسم",
          sortable: true,
          class: "text-center"
        },
        {
          key: "stage",
          label: "المرحلة",
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true
        },
        {
          key: "division",
          label: "الشعبة",
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true
        },

        {
          key: "studentAddress",
          label: "عنوان السكن",
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true
        },

        { key: "abs", label: "تسجيل غائب" }
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      filterOn: [],
      infoModal: {
        id: "info-modal",
        title: "",
        content: ""
      }
    };
  },
  mounted() {
    this.i18n = this.$i18n;
    if (this.enableRTL) {
      this.i18n.locale = "ar";
      this.$rtl.enableRTL();
    }

    let url = base.baseUrl + "getSections";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        // alert(response);
        console.log(response);

        self.sections = response.data;
      })
      .catch(function(error) {
        alert("sponse");
      });
  },

  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  },
  methods: {
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();

      return valid;
    },
    resetModal() {},
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault();

      // Trigger submit handler
      this.handleSubmit();
    },
    handleSubmit() {
      // Exit when the form isn't valid

      // Push the name to submitted names
      this.submittedNames.push(this.name);
      //alert("good");
      // Hide the modal manually

      let url =
        base.baseUrl +
        "getStudents?section=" +
        this.section +
        "&stage=" +
        this.stage +
        "&division=" +
        this.division;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          console.log(response);
          self.items = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });

      this.$nextTick(() => {
        this.$bvModal.hide("modal-prevent-closing");
      });
    },
    getStages() {
      this.stage = "";
      this.division = "";
      let url = base.baseUrl + "getStages?section=" + this.section;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.stages = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    getDivisions() {
      this.division = "";
      let url =
        base.baseUrl +
        "getDivisions?section=" +
        this.section +
        "&stage=" +
        this.stage;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.divisions = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    deleteStudent() {
      let url =
        base.baseUrl + "deleteStudent?studentId=" + this.deleteStudentId;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .delete(url, {
          "Content-Type": "application/x-www-form-urlencoded;charset=utf-8",
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);
          //  alert("تم الحذف");
          if (response.data.true == 1) {
            self.notifyVue(
              "top",
              "center",
              "success",
              "تم حذف الطالب " + self.deleteStudentName
            );
            self.DeletedModelshow = false;
          } else self.notifyVue("top", "center", "danger", "هناك خطأ ما !!");

          let url =
            base.baseUrl +
            "getStudents?section=" +
            self.section +
            "&stage=" +
            self.stage +
            "&division=" +
            self.division;
          let baseToken = base.baseToken;
          let headers = {
            Token: localStorage.auth
          };

          self = self;
          axios
            .get(url, {
              headers
            })
            .then(function(response) {
              console.log(response);
              self.items = response.data;
            })
            .catch(function(error) {
              alert("sponse");
            });
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        group: "foo",
        title: text,
        text: "This is <b> content </b>",
        duration: "100",
        speed: 1000,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: "center",
        verticalAlign: "top",
        type: type,
        ignoreDuplicates: 1,
        Timeout: 800
      });
    },
    getAbsences() {
      let url = base.baseUrl + "studentsAbsences";
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;

      let formData = new FormData();

      formData.append("studentIds", JSON.stringify(self.selected));
      formData.append("subject", self.subject);
      axios
        .post(url, formData, {
          headers
        })
        .then(function(response) {
          console.log(response);
          alert("added");
        })
        .catch(function(error) {
          alert("sponse");
        });
    }
  }
};
</script>
