<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div class="places-buttons">
          <div class="row">
            <div class="col-md-6 ml-auto mr-auto text-center">
              <h4 class="card-title">
                رفع واجب
              </h4>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-8 ml-auto mr-auto">
              <div class="row">
                <div class="col-md-6" style="    margin: auto;">
                  <base-button
                    v-b-modal.classNoti
                    type="primary"
                    @click="getSections()"
                    block
                  >
                    رفع واجب
                  </base-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </card>
    </div>
    <!--<div class="col-md-6">
      <card>
        <h4 slot="header">الواجبات</h4>
        <base-alert
          dismissible
          v-for="adv in allAdv"
          :type="adv.type"
          dismissible
