    <div>-->
      

      <b-modal
        id="classNoti"
        ref="modal"
        title="  فرز"
        style="text-align: right;   height: 540px !important; "
        @ok="addHomeWork"
      >
        <div style="text-align:right;" class="col-md-10 pr-md-1">
          <label style="text-align:right;"> القسم</label>
          <b-form-input
            placeholder=""
            v-model="section"
            @change="getStages()"
            list="my-list-id"
          ></b-form-input>

          <datalist id="my-list-id">
            <option v-for="section in sections">{{ section.section }}</option>
          </datalist>
        </div>

        <div v-show="stages" style="text-align:right;" class="col-md-6 pr-md-1">
          <label style="text-align:right;"> المرحلة</label>

          <b-form-input
            v-model="stage"
            list="my-list-id1"
            @change="getDivisions()"
          ></b-form-input>

          <datalist id="my-list-id1">
            <option v-for="stage in stages">{{ stage.stage }}</option>
          </datalist>
        </div>

        <div
          v-show="divisions"
          style="text-align:right;"
          class="col-md-4 pr-md-1"
        >
          <label style="text-align:right;"> الشعبة</label>

          <b-form-input
            v-model="division"
            v-show="divisions"
            list="my-list-id2"
          ></b-form-input>

          <datalist id="my-list-id2">
            <option v-for="division in divisions">{{
              division.division
            }}</option>
          </datalist>
        </div>

        <div
          style="text-align: right;"
          class="col-md-10 pr-md-1"
          v-show="division"
        >
          <label style="text-align:right;">عنوان الواجب</label>
          <b-form-input
            placeholder="عنوان الواجب"
            v-model="homeWorkTitle"
          ></b-form-input>
        </div>

        <div
          style="text-align: right;"
          class="col-md-10 pr-md-1"
          v-show="division"
        >
          <label style="text-align:right;">نص الواجب</label>
          <b-form-textarea
            placeholder="نص الواجب"
            v-model="homeWorkText"
          ></b-form-textarea>
        </div>
