<script>
//import check from "../check";

import { BaseAlert } from "@/components";
import base from "../set";

import axios from "axios";
export default {
  components: {
    BaseAlert
  },
  data() {
    return {
      section: "",
      sections: "",
      stage: "",
      stages: "",
      division: "",
      divisions: "",
      homeWorkTitle: "",
      homeWorkText: "",
      advText: "",

      type: "",
      allAdv: [{ advText: "loading..." }]
    };
  },
  methods: {
    addHomeWork() {
      //alert(this.studentId);
      let url = base.baseUrl + "addHomeWork";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("section", this.section);
      params.append("stage", this.stage);
      params.append("division", this.division);
      params.append("homeWorkTitle", this.homeWorkTitle);
      params.append("homeWorkText", this.homeWorkText);

      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1) location.reload();
          else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    getSections() {
      let url = base.baseUrl + "getSections";
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.sections = response.data;
        })
        .catch(function(error) {
          alert("sponse");
        });
    },
    getStages() {
      this.stage = "";
      this.division = "";
      let url = base.baseUrl + "getStages?section=" + this.section;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.stages = response.data;
        })
        .catch(function(error) {
          alert(error);
        });
    },
    getDivisions() {
      this.division = "";
      let url =
        base.baseUrl +
        "getDivisions?section=" +
        this.section +
        "&stage=" +
        this.stage;
      let baseToken = base.baseToken;
      let headers = {
        Token: localStorage.auth
      };

      self = this;
      axios
        .get(url, {
          headers
        })
        .then(function(response) {
          // alert(response);
          console.log(response);

          self.divisions = response.data;
        })
        .catch(function(error) {
          alert(error);
        });
    },
    handleOk() {
      //alert(this.studentId);
      let url = base.baseUrl + "addAdvertisement";
      // var d1 = new Date(this.model.studentBirthday);
      // console.log(this.format(d1));
      // alert(format(d1));

      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("advText", this.advText);
      params.append("type", this.type);

      self = this;
      let headers = {
        Token: localStorage.auth
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1) {
            self.notifyVue("bottom", "right", "success", response.data.msg);
            location.reload();
          } else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        component: HomeWorkTemplate,
        title: text,
        type: type,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        duration: 2000,
        speed: 1000,
        timeout: 0
      });
    }
  },
  mounted() {
    this.i18n = this.$i18n;

    this.i18n.locale = "ar";
    this.$rtl.enableRTL();
    let url = base.baseUrl + "getAdvertisements";
    let baseToken = base.baseToken;
    let headers = {
      Token: localStorage.auth
    };

    self = this;
    axios
      .get(url, {
        headers
      })
      .then(function(response) {
        console.log(response);
        self.allAdv = response.data;
        //alert("d");
      })
      .catch(function(error) {
        alert(error);
      });
  },
  computed: {
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL() {
      return this.$rtl.isRTL;
    }
  }
};
</script>
