<script>
import base from "../../set";
import axios from "axios";
import NotificationTemplate from "../Notifications/NotificationTemplate";

import { BaseAlert } from "@/components";

export default {
  props: {
    model: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data: {
    model: {
      studentName: "mmmm",
      phone: "",
      studentSex: "",
      studentAddress: "",
      bloodType: "",
      guardianPhone: "",
      studentEmail: "",
      healthStatus: "",
      idNumber: 0,
      studentAge: 0,
      studentSection: 0,
      studentStage: 0,
      studentDivision: 0,
      studentBirthday: 0,
      dateOfReg: 0
    }
  },
  methods: {
    click() {
      let url = base.baseUrl + "/";
      alert(url);
      var params = new URLSearchParams();
      //alert(this.model.studentName);
      params.append("studentName", this.model.studentName);
      params.append("phone", this.model.phone);
      params.append("studentSex", this.model.studentSex);
      params.append("studentAddress", this.model.studentAddress);
      params.append("bloodType", this.model.bloodType);
      params.append("guardianPhone", this.model.guardianPhone);
      params.append("studentEmail", this.model.studentEmail);
      params.append("healthStatus", this.model.healthStatus);
      params.append("idNumber", this.model.idNumber);
      params.append("studentAge", this.model.studentAge);
      params.append("studentSection", this.model.studentSection);
      params.append("studentStage", this.model.studentStage);
      params.append("studentDivision", this.model.studentDivision);
      params.append("studentBirthday", this.model.studentBirthday);
      params.append("dateOfRegistration", this.model.dateOfRegistration);
      self = this;
      let baseToken = base.baseToken;
      let headers = {
        Token: baseToken
      };
      axios
        .post(url, params, {
          headers
        })
        .then(function(response) {
          console.log(response);
          if (response.data.true == 1)
            self.notifyVue("bottom", "right", "success", response.data.msg);
          else self.notifyVue("bottom", "right", "danger", response.data.msg);
        })
        .catch(function(error) {
          // currentObj.output = error;
        });
    },
    notifyVue(verticalAlign, horizontalAlign, type, text) {
      const color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        group: "foo",
        title: text,
        text: "This is <b> content </b>",
        duration: "100",
        speed: 1000,
        icon: "tim-icons icon-bell-55",
        horizontalAlign: "left",
        verticalAlign: "bottom",
        type: type,
        ignoreDuplicates: 1,
        Timeout: 800
      });
    }
  }
};
headers: new Headers({
  "content-type": "application/json",
  Accept: "application/json",
  "Access-Control-Allow-Credentials": true,
  "Access-Control-Allow-Origin": true
});
</script>
